.error {
  padding: 25px;
  box-shadow: 0px 0px 20px rgb(0 0 0 / 25%);
  background-color: #fff;

  &__img {
    display: block;
    width: 250px;
    height: 250px;
    object-fit: contain;
    margin: 0 auto;
  }

  &__message {
    text-align: center;
  }

  &__buttons {
    display: flex;
    justify-content: center;
    .button {
      margin-top: 13px;
    }
  }
}